.nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0; }
  .nav .nav-list {
    width: 100%;
    display: flex;
    flex-flow: column wrap;
    justify-content: flex-end;
    list-style: none;
    margin: 0; }
  .nav .nav-item {
    height: 100%;
    margin: 0; }
    .nav .nav-item:not(.nav-brand) {
      visibility: hidden;
      display: none; }
    .nav .nav-item:not(.nav-brand) > a {
      background-color: transparent;
      border-width: 0 0 0.25rem 0;
      border-style: solid;
      border-color: transparent;
      color: white;
      display: inline-block;
      font-weight: 700;
      margin: 0 2rem 0 0;
      padding: 1rem 1rem 0.75rem 1rem;
      text-decoration: none;
      text-transform: unset; }
      .nav .nav-item:not(.nav-brand) > a:hover, .nav .nav-item:not(.nav-brand) > a:active, .nav .nav-item:not(.nav-brand) > a:focus {
        background-color: transparent;
        border-width: 0 0 0.25rem 0;
        border-style: solid;
        border-color: #ed2143;
        color: #ed2143;
        font-weight: 700;
        margin: 0 2rem 0 0;
        padding: 1rem 1rem 0.75rem 1rem;
        text-decoration: none;
        text-transform: unset; }
    .nav .nav-item:not(.nav-brand):last-child > a {
      margin: 0 2rem 0 0; }
      .nav .nav-item:not(.nav-brand):last-child > a:hover, .nav .nav-item:not(.nav-brand):last-child > a:active, .nav .nav-item:not(.nav-brand):last-child > a:focus {
        margin: 0 2rem 0 0; }
  .nav.nav--active .nav-item:not(.nav-brand) {
    visibility: visible;
    display: block; }
    .nav.nav--active .nav-item:not(.nav-brand) a {
      display: block; }
  .nav .nav-brand {
    margin-right: auto; }
  .nav .nav-toggle {
    position: absolute;
    top: 10px;
    right: 16px;
    padding: 12px 24px;
    border: none;
    border-radius: 0.25rem;
    background: white;
    font-size: 1rem;
    font-weight: 700;
    cursor: pointer; }
  @media (min-width: 77.5rem) {
    .nav .nav-list {
      flex-flow: row nowrap; }
    .nav .nav-item:not(.nav-brand) {
      visibility: visible;
      display: inline-block; }
      .nav .nav-item:not(.nav-brand) > a {
        transition: all 0.2s ease-out; }
    .nav .nav-toggle {
      visibility: hidden;
      display: none; } }

